.app {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.container {
  position: relative;
  min-width: 1200px;
  min-height: 600px;
  padding: 40px;
  display: flex;
  position: relative;
  font-family: "Calibri", sans-serif;
  color:#1376D1;
  border: 2px solid #1376D1;
}

.title, .profile, .school, .back {
  position: relative;
  flex: 1;
  z-index: 10;
}

.title {
  width: 100%;
}

.school {
  font-size: 14px;
  text-align: right;
}

.pupil {
  padding-right: 10px;
  text-align: right;
}

.lottie-wrapper {
  position: absolute;
  top: 0;
  left: 0;
}

#lottie {
  width: 880px;
}

.human-cross:hover, .question:hover, .sportcar {
  cursor: pointer;
}

.red path[fill="rgb(161,222,255)"] {
  fill: red;
}

